import FlexWrapper from "./FlexWrapper";
import {
  BackgroundImage,
  Heading,
  theme,
} from "@mechatronick/datum-design-system";
import React from "react";
import Fade from "react-reveal/Fade";

const MultipleBackGroundImages = props => {
  const { data } = props;
  return (
    <>
      <FlexWrapper
        style={{
          textAlign: "center",
          backgroundColor: theme.colors["trueWhite"],
        }}
        alignItems={"stretch"}
        wrap={"wrap"}
      >
        {data.map((eachProperty, index) => {
          return (
            <BackgroundImage
              style={{ boxShadow: theme.boxShadows[1] }}
              key={eachProperty.title}
              width={[1, 10 / 11, 5 / 11, 4 / 13]}
              m={[2, 2, 2]}
              p={[2, 2, 3]}
              src={eachProperty.svg}
              aria-label={eachProperty.title}
            >
              <Fade bottom>
                <Heading.h4
                  bold
                  align="center"
                  style={{ fontWeight: "bold", color: "white" }}
                >
                  {eachProperty.title}
                </Heading.h4>
              </Fade>
            </BackgroundImage>
          );
        })}
      </FlexWrapper>
    </>
  );
};

export default MultipleBackGroundImages;
