import {graphql, useStaticQuery} from "gatsby"

export const useCompetenciesPicsHook = () => (
    useStaticQuery(
        graphql`
            fragment squareImage400 on File {
                childImageSharp {
                    fluid(maxWidth: 400) {
                        ...GatsbyImageSharpFluid
                    }
                }
            }
            
            query {
                
                
                BigData: file(relativePath: { eq: "competencies/BigData.jpg" }) {
                    ...squareImage400
                }
                CloudComputing: file(relativePath: { eq: "competencies/CloudComputing.jpg" }) {
                    ...squareImage400
                }
                InternetOfThings: file(relativePath: { eq: "competencies/InternetOfThings.jpg" }) {
                    ...squareImage400
                }
                CyberSecurity: file(relativePath: { eq: "competencies/CyberSecurity.jpg" }) {
                    ...squareImage400
                }
                AugmentedReality: file(relativePath: { eq: "competencies/AugmentedReality.jpg" }) {
                    ...squareImage400
                }
                ArtificialIntelligence: file(relativePath: { eq: "competencies/ArtificialIntelligence.jpg" }) {
                    ...squareImage400
                }
            }
        `
    )
);